import{openModal, closeModal} from './modal';
import{postData} from '../services/services';

function forms (formSelector, modalTimerId) {
  //Forms

  const forms = document.querySelectorAll(formSelector);

  const message = {
    loading: "img/form/spinner.svg",
    success: "!     ",
    failure: "-   ...",
  }; //     ,     

  forms.forEach((item) => {
    bindPostData(item);
  });

  function bindPostData(form) {
    form.addEventListener("submit", (e) => {
      e.preventDefault();
      let statusMessage = document.createElement("img");
      // statusMessage.classList.add("status");
      statusMessage.src = message.loading;
      statusMessage.style.cssText = `
        display: block;
        margin: 0 auto;
      `; //    ,     
      // form.append(statusMessage);
      form.insertAdjacentElement("afterend", statusMessage); //     ,   

      // 2     .   Json    FormData.         - .
      // request.setRequestHeader("Content-type", "multipart/form-data");!!!    XMLHttpRequest  FormData   ()   .   !    .

      const formData = new FormData(form); //  ,           -,     (   JSON!).
      // //!!!  FormData          input (     ,  textarea)    name,  name="name", name="phone"  .
      // const object = {};
      // formData.forEach(function (value, key) {
      //   object[key] = value;
      // }); // FormData   ,          JSON.       .

      // const json = JSON.stringify(object); //          JSON

      const json = JSON.stringify(Object.fromEntries(formData.entries())); //      formData    json.    ,   formData   .  formData.entries()     .

      // fetch("server.php", {
      //   method: "POST",
      //   headers: {
      //     "Content-type": "application/json", //      json,   headers  
      //   },
      //   // body: formData,
      //   body: JSON.stringify(object),
      // })
      postData("http://localhost:3000/requests", json) //      fetch 
        // .then((data) => data.text()) //      , ..     postData (   )
        .then((data) => {
          console.log(data); // data     ,    
          showThanksModal(message.success);
          statusMessage.remove();
        })
        .catch(() => {
          showThanksModal(message.failure);
        })
        .finally(() => {
          form.reset();
        });
    });
  }

  function showThanksModal(message) {
    const prevModalDialog = document.querySelector(".modal__dialog");

    prevModalDialog.classList.add("hide");
    openModal('.modal', modalTimerId);

    const thanksModal = document.createElement("div");
    thanksModal.classList.add("modal__dialog");
    thanksModal.innerHTML = `
      <div class="modal__content">
        <div data-close class="modal__close">&times;</div>
        <div class="modal__title">${message}</div>
      </div>
    `;
    document.querySelector(".modal").append(thanksModal);

    setTimeout(() => {
      thanksModal.remove();
      prevModalDialog.classList.add("show");
      prevModalDialog.classList.remove("hide");
      closeModal('.modal');
    }, 4000);
  }

  fetch(" http://localhost:3000/menu")
    .then((data) => data.json())
    .then((res) => console.log(res));

  //Slider ( )

  // const slides = document.querySelectorAll(".offer__slide"),
  //   prev = document.querySelector(".offer__slider-prev"),
  //   next = document.querySelector(".offer__slider-next"),
  //   total = document.querySelector("#total"),
  //   current = document.querySelector("#current");

  // let slideIndex = 1;

  // showSlides(slideIndex);

  // if (slides.length < 10) {
  //   total.textContent = `0${slides.length}`;
  // } else {
  //   total.textContent = slides.length;
  // } //        showSlides(n), ..         .            .

  // function showSlides(n) {
  //   if (n > slides.length) {
  //     slideIndex = 1;
  //   }
  //   if (n < 1) {
  //     slideIndex = slides.length;
  //   }

  //   // slides.forEach((item) => (item.style.display = "none"));
  //   // slides[slideIndex - 1].style.display = "block";

  //   slides.forEach((item) => item.classList.add("hide"));
  //   slides[slideIndex - 1].classList.remove("hide");
  //   slides[slideIndex - 1].classList.add("show", "fade");

  //   if (slides.length < 10) {
  //     current.textContent = `0${slideIndex}`;
  //   } else {
  //     current.textContent = slideIndex;
  //   }
  // }

  // function plusSlides(n) {
  //   showSlides((slideIndex += n));
  // }

  // prev.addEventListener("click", () => {
  //   plusSlides(-1);
  // });

  // next.addEventListener("click", () => {
  //   plusSlides(1);
  // });

}

export default forms;